import React from 'react';

export function Sponsors(props) {
    return (
        <section className="sponsors">
            <div className="row">
                <div className="col-4">
                    <div className="card">
                        <img src={require("../../images/sul-logo.svg")} alt=''/>
                    </div>
                </div>
                <div className="col-4">
                    <img src={require("../../images/eit-digital-logo.png")} alt=''/>
                </div>
                <div className="col-4">
                    <img src={require("../../images/unitn-logo.png")} alt=''/>
                </div>
            </div>
        </section>
    );
}