import React from 'react';

export function Videos(props) {
    return (
        <section className="video">
            <span id="videos" className="anchor"></span>
            <div className="section-title">
                Watch our video about the validation!
            </div>
            <div className="outer-wrapper">
                <div className="video-wrapper">
                    <iframe title='vimeo1' src="https://player.vimeo.com/video/327973883" className="validation-video" frameBorder="0" webkitallowfullscreen="true" mozallowfullscreen="true" allowFullScreen></iframe>
                </div>
            </div>
        </section>
    );
}