import React from 'react';

import { LandingForm } from '../components/landingForm/landingForm';

export function Landing(props) {
    return (
        <div className='landing'>
            <div className='content'>
            <div className="background"></div>
                <section className="title">
                    <div className="brandname engel">
                        ENGEL
                    </div>
                    <div className="subtitle">
                        Reserve your bottle now
                    </div>
                </section>
                <section className="image">
                    <img src={require('../images/bottle.png')} alt=''/>
                </section>
                <section className="text">
                    <p>
                        Our first <span className="red">100</span> cutomers have the unique opportunity to recive an <span className="engel">ENGEL</span> bottle for just <span className="red">100&euro;</span>
                    </p>
                    <p>
                        This offer is limited to the first 100 customers. The regular price for our bottle is 250&euro;
                    </p>
                </section>
                <section className="form">
                    <LandingForm />
                </section>
            </div>
        </div>
    );
}