import React from 'react';

export function WhatIsEngle(props) {
    return(
        <section className="introduction">
                <span id="learn-more" className="anchor"></span>
                <div className="section-title">
                    What is <span className="engel">ENGEL</span> ?
                </div>
                <div className="row">
                    <div className="col-8">
                        <p className="slogen">Don't worry, Bike happy</p>
                        <p>means that we want you to ride safely and worry less about potential injuries.</p>
                        <p>Among all cyclists mountain bikers have the most injuries and fatalities. The art of the sport is to embrace risks and therefore 70% of all accidents occur without any collision. Phones break, injuries prohibit you to call for help or you simply don’t know your location.</p>
                        <p>Whatever the case, Engel is the solution. It’s a sophisticated electronic device robust and hidden inside your water bottle. More reliable than any smart phone or smart watch - it can detect a crash and report your location directly to emergency contacts you specify yourself. </p>
                        <p>Completely PHONE INDEPENDENT!</p>
                    </div>
                    <div className="col-4">
                        <img src={require("../../images/bottle.png")} alt=""/>
                    </div>
                </div>
            </section>
    );
}