import React from 'react';

export function FaqItem(props) {
    return (
        <div className="faq-item">
            <div className="title">
                {props.title}
            </div>
            <div className="text">
                {!!props.text && props.text.map((article, i) => {
                    return (
                        <p key={i}>
                            {article}
                        </p>
                    );
                })}
            </div>
        </div>
    );
}