import React from 'react';

export class CookieBanner extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            showBanner: true
        }

        this.acknowledgeCookie = this.acknowledgeCookie.bind(this);
    }

    componentDidMount() {
        if (isCookieBannerAcknowledged()) {
            this.setState({
                showBanner: false
            });
        }
    }

    acknowledgeCookie() {
        const time = new Date();
        const expire = time.setFullYear(time.getFullYear() + 3);
        document.cookie = `acknowledge-cookie=1; max-age=${expire}`;
        this.setState({
            showBanner: false
        });
    }

    render() {
        return this.state.showBanner ? (
            <div id="cookie">
                <div className="card">
                    <p>This site use cookies.</p>
                    <p><a href={require('../../files/Privacy Policy engel.pdf')}>Learm more</a></p>
                    <button onClick={this.acknowledgeCookie}>Got it!</button>
                </div>
            </div>
        ) : null ;
    }
}

const isCookieBannerAcknowledged = () => {
    return document.cookie.split(';').filter(function(item) {
        return item.trim().indexOf('acknowledge-cookie=') == 0
    }).length;
}