import React from 'react';

export function Statistic(props) {
    return(
        <section className="statistic">
            <span id="statistic" className="anchor"></span>
            <div className="section-title">
                Facts
            </div>
            <div className="row">
                <div className="col-4">
                    <div className="card">
                        <div className="statistic-title">Riding</div>
                        <a href={require("../../images/riding-with.png")} target="_blank">
                            <img src={require("../../images/riding-with.png")} alt='' />
                        </a>
                            <p>Mountain Bikers often ride alone. Despite the fun, an accident could be fatal. Imagine you break your ribs, spine or collar bone and smash your phone. Even if you are able to call for help, can you accurately describe your location? How long will it take for help to find you? Engel detects a crash and calls for help, even if you are unconscious. It calls help and sends your exact location.</p>
                    </div>
                </div>
                <div className="col-4">
                    <div className="card">
                        <div className="statistic-title">Injuries</div>
                        <p>22% percent of all crashes result in major injuries. That’s a high percentage! Don’t be the 1 of 5 that have this problem. Don’t worry, bike happy, with Engel.</p>
                        <a href={require("../../images/injuries-diagramm.jpg")} target="_blank">
                            <img src={require("../../images/injuries-diagramm.jpg")} alt='' />
                        </a>
                    </div>
                </div>
                <div className="col-4">
                    <div className="card">
                        <div className="statistic-title">Accidents</div>
                        <a href={require("../../images/accident-diagramm.jpg")} target="_blank">
                            <img src={require("../../images/accident-diagramm.jpg")} alt='' />
                        </a>
                        <p>Accidents occur regularly, however in contrast to road bikers, mountain bike riders often fall without exterior causes. Everyone can be unlucky. Don’t take your chances, don’t get injured again. You don’t want to lay in hospital for weeks, just because you didn’t get the right help.</p>
                    </div>
                </div>
            </div>
        </section>
    );
}