import React from 'react';
import { classname } from '../../actions/classNames';
import { Link } from 'react-router-dom';

import { Bookmarks } from '../../functions/bookmarks/bookmarks';

export class NavBar extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            isMenuOpen: false
        }

        this.toggleMenu = this.toggleMenu.bind(this);
        this.closeMenu = this.closeMenu.bind(this);
    }
    
    toggleMenu() {
        this.setState({
            isMenuOpen: !this.state.isMenuOpen
        });
    }

    closeMenu() {
        this.setState({
            isMenuOpen: false
        });
    }

    render() {
        return (
            <nav className={classname("topnav", this.state.isMenuOpen && "responsive")} id="res-nav" onBlur={this.closeMenu} >
                <div className={classname("menu-icon","white")} onClick={this.toggleMenu}></div>
                <div className="links">
                    {!!this.props.links && this.props.links.map((link, i) => {
                        return (<Link to={link.url} key={i}>{link.name}</Link>);
                    })}
                </div>
                <hr className="no-desktop" />
                <div className="bookmarks" onClick={this.closeMenu}>
                    <Bookmarks bookmarks={this.props.bookmarks}/>
                </div>
            </nav>
        );
    }
}
