import React from 'react';

export function Brand(props) {
    return (
        <section className="brand">
            <div className="row">
                <div className="col-6 text">
                    <div className="brand-name">ENGEL</div>
                    <div className="brand-slogen">Don't worry, Bike happy</div>
                    <div className="more-link">
                        <a href="#learn-more">Learn More</a>
                        <a href="#email-form">Get Yours</a>
                    </div>
                </div>
                <div className="col-6 logo">
                    <img className="brand-logo" src={require("../../images/logo.png")} alt="" />
                </div>
            </div>
        </section>
    );
}