import React from 'react';

import { NavBar } from '../components/navBar/navBar';
import { Brand } from '../functions/brand/brand';
import { WhatIsEngle } from '../functions/whatIsEngle/whatIsengle';
import { EmailForm } from '../components/emailForm/emailForm';
import { Statistic } from '../functions/statistic/statistic';
import { Videos } from '../functions/videos/videos';
import { Sponsors } from '../functions/sponsors/sponsors'; 
import { Footer } from '../components/footer/footer';
import PageURL from '../constants/pageUrl.js';

export class Home extends React.Component {

    render() {

        const bookmarks = {
            name: ['Learn More', 'Get Yours', 'Facts', 'Watch Our Video'],
            url: ['learn-more', 'email-form', 'statistic', 'videos']
        }

        const navProps = {
            links: PageURL,
            bookmarks
        }

        const footerProps = {bookmarks};

        return (
            <div>
                <NavBar {...navProps} />
                <Brand />  
                <WhatIsEngle />
                <EmailForm />
                <Statistic />
                <Videos />
                <Sponsors />
                <Footer {...footerProps} />
            </div>
        );
    }
}