import React from 'react';

export function Bookmarks(props) {
    return (
        <div>
            {!!props.bookmarks && props.bookmarks.name.map((bookmark, i) => {
                return (
                    <a
                        href={`#${props.bookmarks.url[i]}`}
                        key={i}>
                            {bookmark}
                    </a>
                );
            })}
        </div>
    );
}