import React from 'react';
import { ajax } from '../../services/ajax';
import { InfoCard } from '../../functions/infoCard/infoCard';


export class LandingForm extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            isStarted: false,
            showCard: false,
            firstName: null,
            lastName: null,
            email: null,
            address: null,
            country: null,
            city: null,
            zipCode: null,
        }

        this.startHandler = this.startHandler.bind(this);
        this.formHandler = this.formHandler.bind(this);
        this.submitOrder = this.submitOrder.bind(this);
        this.checkbox = this.checkbox.bind(this);
        this.infocardClosed = this.infocardClosed.bind(this);

    }

    infocardClosed() {
        this.setState({
            showCard: false
        });
    }

    startHandler() {
        this.setState({
            isStarted: true,
        });
    }

    formHandler(event) {
        this.setState({
            [event.target.id]: event.target.value,
        });
    }

    submitOrder() {
        if (!this.checkbox()) {
            return;
        }
        this.setState({
            isSending: true,
            showCard: true,
            info: {
                title: 'Thank you for your order.',
                subTitle: 'Please wait until we register it.',
                text: null,
                button: false
            }
        });
        ajax('https://j21bzza060.execute-api.eu-central-1.amazonaws.com/prod/order', 'POST' , {
                firstName: this.state.firstName,
                lastName: this.state.lastName,
                email: this.state.email,
                address: this.state.address,
                country: this.state.country,
                city: this.state.city,
                zipCode: this.state.zipCode,
            }).then(
                    (success) => {
                        this.setState({
                            isSending: false,
                            info: {
                                title: 'Thank you for your order.',
                                subTitle: success.message,
                                text: null,
                                button: true
                            }
                        });
                    },
                    (error) => {
                        this.setState({
                            isSending: false,
                            info: {
                                title: 'Ops!',
                                subTitle: 'There were some network error. Please try leater.',
                                text: null,
                                button: true
                            }
                        });
                    }
                )
    }

    checkbox() {
        if(!document.getElementById('privacy').checked) {
            this.setState({
                showCard: true,
                info: {
                    title: 'First you have to accept our privacy policy!',
                    subTitle: null,
                    text: 'Please accept our privacy policy and try again.',
                    button: true
                }
            });
            return false;
        }
    
        if(!document.getElementById('purchase').checked) {
            this.setState({
                showCard: true,
                info: {
                    title: 'First you have to confirm that you really want it!',
                    subTitle: null,
                    text: 'Please confirm it and try again.',
                    button: true
                }
            });
            return false;
        }

        return true;
    }
    
    render() {
        return (
            !this.state.isStarted ?

            <button className='button' onClick={this.startHandler}>Book Now</button>
            :
            <div>
                <div className="form-title">Personal information</div>
                <form>
                    <label htmlFor="firstName">First Name</label>
                    <input id="firstName" type="text" onChange={e => this.formHandler(e)}/>
                    <label htmlFor="lastName">Last Name</label>
                    <input id="lastName" type="text" onChange={e => this.formHandler(e)}/>
                    <label htmlFor="email">Email Address</label>
                    <input id="email" type="email" onChange={e => this.formHandler(e)}/>
                </form>
                <div className="form-title">Shipping Information</div>
                <form>
                    <label htmlFor="address">Address</label>
                    <input id="address" type="text" onChange={e => this.formHandler(e)}/>
                    <label htmlFor="country">Country</label>
                    <input id="country" type="text" onChange={e => this.formHandler(e)}/>
                    <label htmlFor="city">City</label>
                    <input id="city" type="text" onChange={e => this.formHandler(e)}/>
                    <label htmlFor="zipCode">Zip Code</label>
                    <input id="zipCode" type="text" onChange={e => this.formHandler(e)}/>
                </form>
                <div className="text">
                    <p>Thank you for your interest in the <span className="engel">ENGEL</span> bottle!</p>
                    <p>Once your <span className="engel">ENGEL</span> has been produced we will notify you via e-mail. You will have one week to purchase it. No payments are required at the moment. Once you recived the mail that you bottle is ready - we kindly ask you to purchase it within 7 days. In case you are not interested anymore please click the NO INTEREST ANYMORE button in the confirmation mail.</p>
                </div>
                <form className="accept">
                    <div>
                        <input id="privacy" type="checkbox"/>
                        <label htmlFor="privacy">I have read and I confirm with the <a href={require('../../files/Privacy Policy engel.pdf')} target="_blank">privacy agreement</a>.</label>
                    </div>
                    <div>
                        <input id="purchase" type="checkbox"/>
                        <label htmlFor="purchase">I confirm that I want to reserve my <span className="engel">ENGEL</span> bottle for a later purchase.</label>
                    </div>
                </form>
                <button className='button' disabled={this.state.isSending} onClick={this.submitOrder}>Order</button>
                {this.state.showCard && <InfoCard 
                    {...this.state.info}
                    closeCard={this.infocardClosed}
                />}
            </div>
        );
    }
}