import React from 'react';

import { Bookmarks } from '../../functions/bookmarks/bookmarks';

export class Footer extends React.Component {

    render() {
        return (
            <footer>
                <div className="row">
                    <div className="col-4">
                        <div className="brand-name">
                            ENGEL
                            </div>
                        <div className="footer-brand-text">
                            <p>The IoT device which saves your life.</p>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="footer-title">
                            Contacts Us
                            </div>
                        <ul className="footer-contacts">
                            <li><a href="https://www.facebook.com/engelbottle/" target="_blank">Like us on facebook</a></li>
                            <li>engel.customer@outlook.com</li>
                            <li>engelbikes@gmail.com</li>
                        </ul>
                    </div>
                    <div className="col-4">
                        <div className="footer-title">
                            Page navigation
                        </div>
                        <div className="footer-links">
                            <Bookmarks bookmarks={this.props.bookmarks} />
                        </div>
                    </div>
                </div>
            </footer>
        );
    }
}