import React from 'react';

export function InfoCard(props) {
    
    function close() {
        props.closeCard();
    };

    return (
        <div id="info-card">
            <div className="title">
                {props.title}
            </div>
            {!!props.subTitle && <div className="sub-title">
                {props.subTitle}
            </div>}
            {!!props.text && <div className="text">
                {props.text}
            </div>}
            {!!props.button && <div className="button-line">
                <button className='button' type='button' onClick={close}>Close</button>
            </div>}
        </div>
    )
}