import React from 'react';

import { NavBar } from '../components/navBar/navBar';
import { Footer } from '../components/footer/footer';
import PageURL from '../constants/pageUrl.js';

export function Team(props) {

    const bookmarks = {
        name: ['Lucrezia', 'Ludovico', 'Cesar', 'Gábor', 'Marvin', 'Federico', 'Adriano'],
        url: ['lucrezia', 'ludovico', 'cesar', 'gabor', 'marvin', 'federico', 'adriano']
    }

    const navProps = {
        links: PageURL,
        bookmarks
    }

    const footerProps = {bookmarks};
    
    return (
        <div>
            <NavBar {...navProps} />
            <section className="team-pic">
                <img src={require("../images/team/Team.jpg")} alt='' />
            </section>
            <section className="profiles">
                <div className="row">
                    <div className="col-6">
                        <span id="lucrezia" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture lucrezia"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Lucrezia Pedani
                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-6">
                        <span id="ludovico" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture ludovico"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Ludovico Bianco
                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
    
                <div className="row">
                    <div className="col-6">
                        <span id="cesar" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture cesar"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Cesar Gonzalez
                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-6">
                        <span id="gabor" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture gabor"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Gábor Major
                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
    
                <div className="row">
                    <div className="col-6">
                        <span id="marvin" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture marvin"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Marvin Mouroum
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-6">
                        <span id="federico" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture federico"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Federico Favia
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
    
                <div className="row">
                    <div className="col-3 no-mobile"></div>
                    <div className="col-6">
                        <span id="adriano" className="anchor"></span>
                        <div className="profile-card">
                            <div className="row">
                                <div className="col-5 profile-picture adriano"></div>
                                <div className="col-7">
                                    <div className="name">
                                        Adriano Fontanari
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
    
            </section>
            <Footer {...footerProps} />
        </div>
    );
}