import React from 'react';
import { NavBar } from '../components/navBar/navBar';

import { FaqItem } from '../functions/faqItem/faqItem';
import { FaqItems } from '../constants/faqItems';
import PageURL from '../constants/pageUrl.js';

export function Faq(props) {

    const navProps = {
        links: PageURL,
        bookmarks: null
    }

    return (
        <div>
            <NavBar {...navProps} />
            <div id="faq">
                {!!FaqItems && FaqItems.map((item, i) => {
                    return (
                        <FaqItem
                        key={i}
                        title={item.title}
                        text={item.text}
                        />
                        )
                    })}
            </div>
        </div>
    );
}